<template>
    <div>
        <div class="app">
            <!--  广告入口  -->
            <div class="ad">
                <router-link to="/treestrategy"><img src="@/static/images/tree/shequn_image.png" alt=""></router-link>
            </div>
            <van-list
                    v-if="listData.length"
                    v-model="loading"
                    :finished="finished"
                    finished-text=""
                    @load="getList"
            >
            <ul class="list">
                <li v-for="item in listData">
                    <router-link :class="{tag:item.creater}" :to="{path:'/treefinance',　query: {treeId: item.id}}">
                        <img src="@/static/images/tree/caifushupage_logo.png" alt />
                        <p>{{item.name}}</p>
                        <p><span>使用币种：</span>{{item.currency_name}}</p>
                        <p><span>层级：</span>{{item.level}}层</p>
                        <!--<p>金额：<span v-for="(val, index) in item.upgrade_money">
                           升{{orderText[index]}}级{{val}}个币、
                        </span>
                        </p>-->
                        <p>推广限制：<span v-if="item.limit > 0">一推{{item.limit}}</span> <span v-else>无限制</span></p>
                        <p>当前参与人数<span>{{item.join_count}}</span>人</p>
                    </router-link>
                </li>
            </ul>
            </van-list>
            <div v-if="!listData.length" class="searchresult">
                <img src="@/static/images/tab/wujilv_image.png" alt="">
                <p>无记录, 赶快去创建~</p>
            </div>
        </div>
        <tab-bar></tab-bar>
    </div>


</template>

<script>
    import tabBar from '@/components/tabBar'
    import { treeList } from '@/api/tree'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import { List } from "vant";
    import 'vant/lib/list/style';
    export default {
        name: "treeindex",
        components: {
            tabBar,
            [List.name]: List,
        },
        data()
        {
            return {
                loading:false,
                finished:false,
                page:1,
                listData:[],
                orderText:['0', '一', '二', '三', '四', '五', '六', '七', '八', '九', '十', '十一', '十二', '十三', '十四','十五', '十六', '十七', '十八']
            }
        },
        mounted()
        {
            //this.getList()
        },
        methods:{

            // 我的财富树列表
            getList()
            {
                this.loading = true
                treeList({page:this.page}).then(res => {
                    this.loading = false
                    if (this.page == 1)
                    {
                        this.listData = []
                    }
                    this.page++
                    if (res.length < 20)
                    {
                        this.finished = true
                    }
                    this.listData = this.listData.concat(res)
                })
            }
        },
        // 数据暂时不用缓存信息
        activated(){
            this.page = 1;
            this.getList()
        }
    }
</script>

<style scoped>
    .app .ad {
        padding: 0.06rem 0.1rem;
    }
    .app .ad img {
        width: 100%;
        height: 0.98rem;
    }
    .app .list {
        padding: 0 0.15rem 0 0.15rem;
        box-sizing: border-box;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .app .list li {
        width: 1.68rem;
        background-color: #fff;
        margin-bottom: 0.1rem;
        box-shadow: 0 0 0.09rem 0 rgba(0, 0, 0, 0.1);
        text-align: center;
    }
    .app .list li a {
        display: block;
        padding: 0 0.1rem 0.17rem 0.1rem;
        font-size: .11rem;
        position: relative;
    }
    .app .list li .tag:before {
        position: absolute;
        top: 5px;
        left: -8px;
        z-index: 1;
        padding-right: 6px;
        font-weight: bold;
        color: #fff;
        height: 0;
        line-height: 0;
        border: #e75b51 9px solid;
        border-right-color: transparent;
        content: "创建者";
        box-shadow: 0px 5px 4px -5px #000;
    }
    .app .list li .tag:after {
        content: "";
        position: absolute;
        top: 23px;
        left: -8px;
        border: #89540c 4px solid;
        border-left-color: transparent;
        border-bottom-color: transparent;
    }
    .app .list li a img {
        width: 0.5rem;
        display: block;
        margin: auto;
        padding-top: 0.17rem;
    }
    .app .list li a p:nth-child(2) {
        font-size: 0.15rem;
        color: #000000;
        margin-top: 0.13rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .app .list li a p:nth-child(3) {
        font-size: 0.11rem;
        color: #666666;
        margin-top: 0.06rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .app .list li a p:nth-child(4) {
        font-size: 0.11rem;
        color: #666666;
        margin-top: 0.06rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .app .list li a p:nth-child(5) {
        font-size: 0.11rem;
        color: #666666;
        margin-top: 0.06rem;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        line-clamp: 2;
        -webkit-box-orient: vertical;
    }
    .app .list li a p:nth-child(6) {
        font-size: 0.11rem;
        color: #666666;
        margin-top: 0.06rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .app .list li a p:nth-child(7) {
        font-size: 0.12rem;
        color: #666666;
        margin-top: 0.09rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .searchresult {
        height: 70vh;
        text-align: center;
    }
    .searchresult img {
        width: 1.1rem;
        height: 1rem;
        display: block;
        margin: auto;
        padding-top: 1rem;
    }
    .searchresult p {
        font-size: 0.14rem;
        color: #666666;
        margin-top: 0.2rem;
    }
</style>



